<template>
  <div id="single-blog">
    <h2>{{data.title}}</h2>
    <div>{{data.content}}</div>
    <div>作者:{{data.author}}</div>
    <div>分类:
      <ul>
        <li v-for="(value,index) in data.category" :key="index">{{value}}</li>
      </ul>
    </div>
    <button @click="removeBlog">删除</button>
    <router-link :to="'/edit/' + id ">编辑</router-link>
  </div>
</template>

<script>

export default {
  data() {
    return {
      // 路由route中的parmas属性存储了URL拼接的数据
      id: this.$route.params.id,
      data: {},

    };
  },
  created() {
    // 后面要拼接的参数要使用url+/id的形式
    this.$axios
      .get("/posts/" + this.id + ".json")
      .then(result => {
        this.data = result.data;
      });
  },
  methods: {
    removeBlog: function() {
      this.$axios
        .delete("/posts/" + this.id + ".json")
        .then(() => {
          this.$router.push({ path: "/" });
        });
    }
  }
};
</script>

<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px;
}
ul {
  margin: 0;
  list-style-type: none;
}

</style>
