<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题:</label>
      <input type="text" v-model="bolg.title" required>
      <label>博客内容:</label>
      <textarea v-model="bolg.content"></textarea>
      <div id="checkboxs">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="bolg.category">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="bolg.category">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="bolg.category">
        <label>Angular.js</label>
        <input type="checkbox" value="Angular.js" v-model="bolg.category">
      </div>
      <label>作者:</label>
      <select v-model="bolg.author">
        <option v-for="author in authors" :key="author">{{author}}</option>
      </select>
      <button @click.prevent="post">编辑博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客已经发布成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{bolg.title}}</p>
      <p>博客内容:</p>
      <p>{{bolg.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="itme in bolg.category" :key="itme">{{itme}}</li>
      </ul>
      <p>作者:{{bolg.author}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "add-blog",
  data() {
    return {
      bolg: {},
      id: this.$route.params.id,
      authors: ["小双", "盛哥", "放哥"],
      submmited: false
    };
  },
  methods: {
    post() {
      this.$axios
        .put("/posts/" + this.id + ".json",this.bolg)
        .then(() => {
          this.submmited = true;
        });
    },
    get(){
        this.$axios.get("/posts/" + this.id + ".json").then(res=>{
            this.bolg = res.data;
        })
    },
  },
  created () {
     this.get()
  }
};
</script>
<style scoped>
#add-blog * {
  box-sizing: border-box;
}
#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}
textarea {
  height: 200px;
}
#checkboxs label {
  display: inline;
}
#checkboxs input {
  display: inline-block;
  margin-right: 10px;
}
button {
  display: block;
  margin: 20px 0;
  padding: 14px;
  background: crimson;
  color: #fff;
  border: 0;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3 {
  margin-top: 10px;
}
</style>


