<template>
  <div id="show-blogs">
    <h2>博客总览</h2>
    <input type="text" v-model="search" placeholder="请搜索关键字">
      <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
        <router-link :to="'/blog/'+blog.id">
          <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
        </router-link>
        <article>{{blog.content | snippet}}</article>
      </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      // 这是一个数组,我曹
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$axios
      .get("/posts.json")
      .then(result => {
        let data = result.data;
        for (let key in data) {
          // 给每个对象添加一个id值就是当前的键名
          data[key].id = key;
          this.blogs.push(data[key]);
        }
      });
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  directives: {
    rainbow: {
      bind(el) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    },
    // theme: {
    //   bind(el, binding) {
    //     if (binding.value == "wide") {
    //       el.style.maxWidth = "1260px";
    //     } else if (binding.value == "narrow") {
    //       el.style.maxWidth = "560px";
    //     }
    //     if (binding.arg == "column") {
    //       el.style.backgroundColor = "#6677cc";
    //       el.style.padding = "20px";
    //     }
    //   }
    // }
  },
  filters: {
    "to-uppercase"(value) {
      return value.toUpperCase();
    },
    snippet(value) {
      return value.slice(0, 100) + "...";
    }
  }
};
</script>


<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px;
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid #aaa;
}
#show-blogs a {
  color: #444;
  text-decoration: none;
}
input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  margin: 0px 20px;
}
h2 {
  padding: 0 20px;
}

</style>
